.sabias{
    background-color: $verde;
    font-size: 2.4rem;
    padding: 1rem;
    color: $blanco;
    border-radius: 2rem;

    blockquote{
        padding-left: 5rem;
        position: relative;

        &::before{
            content:'';
            background-image: url("../../img/comilla.svg");
            background-repeat: no-repeat;
            width: 4rem;
            height: 6rem;
            display: block;
            position: absolute;
            left:-2%;
        }
    }
    p{
        text-align: right;
        color: $blanco;
    }
    h3{
        font-weight: $bold;
        margin: 0;
    }
    
}